import { Node } from '../../node';
import { Container } from '../../container';
import UuidUtils from '../../utils/uuid-utils';

class UuidNode extends Node {
  constructor() {
    super();
    this.title = 'UUID';
    this.description =
      'A node function to create a UUID identifier. A universally unique identifier (UUID) is an identifier standard used in software construction. A UUID is simply a 128-bit value. The meaning of each bit is defined by any of several variants. For human-readable display, many systems use a canonical format using hexadecimal text with inserted hyphen characters. For example : de305d54-75b4-431b-adb2-eb6b9e546014';
    this.addInput('trigger');
    this.addOutput('uuid-out-8');
    this.addOutput('uuid-out-12');
    this.addOutput('uuid-out-28');
  }

  onInputUpdated() {
    const out8 = UuidUtils.create8DigId();
    const out12 = UuidUtils.create12DigId();
    const out28 = UuidUtils.createUUID();
    this.setOutputData(0, out8);
    this.setOutputData(1, out12);
    this.setOutputData(2, out28);

  }

  onAfterSettingsChange() {
    this.onInputUpdated()
  }

}

Container.registerNodeType('custom-function/uuid', UuidNode);
