import { Container } from '../../container';
import { FlexibleNode } from '../../flexible-node';
import { Type } from '../../node-io';

class ArrayBuilderNumNode extends FlexibleNode {
  constructor() {
    super();
    this.dynamicInputsType = Type.NUMBER;
    this.title = 'Array Builder Number';
    this.description =
      "Outputs an array of values based on the input values as numbers";
    this.addOutput('out', Type.STRING);
  }

  onAdded() {
    this.size = this.computeSize();
    this.onInputUpdated();
  }

  onInputUpdated() {
    let out = []
    for (let i:any = this.dynamicInputStartPosition; i < this.getInputsCount(); i++) {
      out.push(this.getInputData(parseInt(i)))
    }
    this.setOutputData(0, out);
  }

  onAfterSettingsChange() {
    super.onAfterSettingsChange();
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/array-builder-number', ArrayBuilderNumNode);
