import * as express from 'express';
import PlatformDataSource from '../../database/datasource/PlatformDataSource';
import { Container } from '../../nodes/container';
import UuidUtils from '../../nodes/utils/uuid-utils';
import { exec } from 'child_process';
import FileUtils from '../../nodes/utils/file-utils';
import OsUtils from '../../nodes/utils/os-utils';
import IpAddressUtils from '../../nodes/utils/ip-address-utils';

const set_ip_address = require('set-ip-address'); //for setting IPs
const ip = require('ip'); //for checking if IPs are valid

let router = express.Router();

// get platform data
router.get('', async function (req, res) {
  try {
    const platform = await PlatformDataSource.getPlatform();
    res.json(platform);
  } catch (e) {
    res.status(500).send(e);
  }
});

// update platform
router.put('', async function ({ body }, res) {
  try {
    await PlatformDataSource.updatePlatform(body);
    res.status(201).send("successful");
  } catch (e) {
    res.status(500).send(e);
  }
});

// restart wires 
router.get('/wires/runtime/restart', async function (req, res) {
  let command = 'sudo systemctl restart nubeio-rubix-wires.service'
  try {
    //reboot after 10 sec
    setTimeout(() => {
      exec(command, (err, stdout, stderr) => {
        if (stdout) {
          res.json("reboot wires");
        }
        if (err) {
          res.status(500);
        }
        if (stderr) {
          res.status(500);
        }
      });
    }, 5000);
  } catch (e) {
    res.status(500).send(e);
  }
});

// reboot host 
router.get('/host/reboot', async function (req, res) {
  let command = 'sudo reboot'
  try {
    //reboot after 10 sec
    setTimeout(() => {
      exec(command, (err, stdout, stderr) => {
        if (stdout) {
          res.json("reboot host");
        }
        if (err) {
          res.status(500);
        }
        if (stderr) {
          res.status(500);
        }
      });
    }, 5000);
  } catch (e) {
    res.status(500).send(e);
  }
});

// delete db files
// dashboard.db, nodes.db, schedule.db
// sudo touch nodes.db  && sudo touch schedule.db && sudo touch dashboard.db
router.get('/wires/db/delete', async function (req, res) {
  const path = '/data/rubix-wires'
  const nodes = `${path}/nodes.db`
  const sch = `${path}/schedule.db`
  const dashboard = `${path}/dashboard.db`
  const files = [nodes, sch, dashboard];
  files.forEach(function (file) {
    if (FileUtils.checkFileExists(file)) {
      let command = `sudo rm ${file}`
      try {
        exec(command, (err, stdout, stderr) => {
          if (stdout) {
            res.status(201).send("successful");
          }
          if (err) {
            res.status(500);
          }
          if (stderr) {
            res.status(500);
          }
        });
      } catch (e) {
        res.status(500).send(e);
      }
    }
  });
  res.status(201).send("successful");

});

//make uuid
router.get('/uuid/eight', async function (req, res) {
  try {
    const out8 = UuidUtils.create8DigId();
    if (out8) {
      res.json(JSON.stringify(out8));
    } else {
      res.status(500);
    }
  } catch (e) {
    res.status(500).send(e);
  }
});

//download nodes
router.get('/download/nodes', async function (req, res) {
  try {
    const container = Container.containers[0];
    container.db.getNodes((err, docs) => {
      if (!err) {
        res.json(JSON.stringify(docs));
      } else {
        res.status(500);
      }
    });
  } catch (e) {
    res.status(500).send(e);
  }
});

//get disc space
router.get('/host/disc', async function (req, res) {
  try {
    OsUtils.fsSize()
      .then(obj => {
        res.json(JSON.stringify(obj));
      })
      .catch(err => {
        res.status(500).send(err);
      });
  } catch (e) {
    res.status(500).send(e);
  }
});

// get eth1 
router.get('/host/ip/eth1', async function (req, res) {
  try {
    const eth = await PlatformDataSource.getEth1();
    res.json(eth);
  } catch (e) {
    res.status(500).send(e);
  }
});

// update eth1
router.put('/host/ip/eth1', async function ({ body }, res) {
  try {
    await PlatformDataSource.updateEth1(body);
    const interfaceEth0 = 'eth0'; //eth0 (ETH1 on the RC)
    const dhcp = body.dhcpEth1;
    const ip = body.eth1Ip;
    const sub = body.eth1Sub;
    const gate = body.eth1Gate;
    const name = body.eth1NameServer;
    IpAddressUtils.updateIpRubixCompute(dhcp, ip, sub, gate, name, interfaceEth0).then(e => {
      res.status(201).send('UPDATE IP ETH1');
    }).catch(err => {
      res.status(500).send(err);
    })
  } catch (err) {
    res.status(500).send(err);
  }
});


