import { Node, SettingType } from '../../node';
import { Type } from '../../node-io';
import { Container, Side } from '../../container';
import tagRefs from '../../utils/points/point-ref-utils';
import { isNull, isJSON } from '../../../utils/helper';

class PointTagNode extends Node {
  deviceId = 0;
  pointId = 1;
  clientId = 2;
  siteId = 3;
  clientName = 4;
  siteName = 5;
  siteAddress = 6;
  state = 7;
  country = 8;
  lat = 9;
  lon = 10;
  equipRef = 11;
  manualTags = 12;

  constructor() {
    super();
    this.title = 'Point Tag Node';
    this.description = 'A node generating HVAC tags';
    this.settings['tags_group'] = {
      description: 'Tag builder settings',
      value: '',
      type: SettingType.GROUP,
    };
    this.settings['equipType'] = {
      description: 'Equipment Type',
      type: SettingType.DROPDOWN,
      config: {
        items: tagRefs.tagCategory,
      },
    };
    this.settings['tags'] = {
      description: 'Point Type',
      value: null,
      type: SettingType.DROPDOWN,
    };
    this.settings['tags_values'] = {
      description: 'Auto generated tags values',
      value: null,
      type: SettingType.READONLY,
    };
    this.settings['manualSettings'] = {
      description: 'Enter manual (optional)',
      value: false,
      type: SettingType.BOOLEAN,
    };
    this.settings['manualEquip'] = {
      description: 'User equip (optional)',
      value: 'CHILLER',
      type: SettingType.STRING,
    };
    this.settings['manualSensor'] = {
      description: 'User sensor (optional)',
      value: 'Supply Air Zone Sensor',
      type: SettingType.STRING,
    };
    this.settings['point_group'] = {
      description: 'Device details',
      value: '',
      type: SettingType.GROUP,
    };
    this.addInputWithSettings('deviceId', Type.STRING, 'device id', 'device id', false);
    this.addInputWithSettings('pointId', Type.STRING, 'point id', 'point id/name', false);
    this.settings['site_group'] = {
      description: 'Site details',
      value: '',
      type: SettingType.GROUP,
    };
    this.addInputWithSettings('clientId', Type.STRING, 'client id', 'client id', false);
    this.addInputWithSettings('clientName', Type.STRING, 'client name', 'Client Name', false);
    this.addInputWithSettings('siteId', Type.STRING, 'site id', 'site id', false);
    this.addInputWithSettings('siteName', Type.STRING, 'site name', 'Site/Building Name', false);
    this.addInputWithSettings('siteAddress', Type.STRING, '123 my street', 'Site/Building Address', false);
    this.addInputWithSettings('state', Type.STRING, 'NSW', 'State', false);
    this.addInputWithSettings('country', Type.STRING, 'Australia', 'Country', false);
    this.addInputWithSettings('lat', Type.STRING, '', 'Site/Building Latitude', false);
    this.addInputWithSettings('lon', Type.STRING, '', 'Site/Building Longitude', false);
    this.addInputWithSettings('equipRef', Type.STRING, 'FCU123', 'Equipment Reference', false);
    this.addInputWithSettings('manualTags', Type.JSON, 'sensor, ahu', 'manual Tags', false);
    this.addOutput('output', Type.JSON);

  }


  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    const deviceId = this.getInputData(this.deviceId);
    const pointId = this.getInputData(this.pointId);
    const clientId = this.getInputData(this.clientId);
    const clientName = this.getInputData(this.clientName);
    const siteId = this.getInputData(this.siteId);
    const siteName = this.getInputData(this.siteName);
    const siteAddress = this.getInputData(this.siteAddress);
    const state = this.getInputData(this.state);
    const country = this.getInputData(this.country);
    const lat = this.getInputData(this.lat);
    const lon = this.getInputData(this.lon);
    const equipRef = this.getInputData(this.equipRef);
    let equipType = this.settings['equipType'].value;

    //manual settings
    const manualSettings = this.settings['manualSettings'].value
    const manualEquip = this.settings['manualEquip'].value
    const manualSensor = this.settings['manualSensor'].value
    const manualTags = this.getInputData(this.manualTags);

    let tagSetting = tagRefs.tagType(equipType)
    if (isNull(tagSetting)) return;
    this.settings['tags'].config = {
      items: Object.keys(tagSetting),
    };
    let pointType = this.settings['tags'].value
    let tags: any;

    if (manualSettings) {
      pointType = manualSensor;
      equipType = manualEquip;
      tags = manualTags;
    } else {
      for (var key in tagSetting) {
        if (key == pointType) {
          tags = tagSetting[key]
        }
      };
    }
    let pointArr = { pointType: pointType, equipRef: equipRef, equipType: equipType, deviceId: deviceId, pointId: pointId, clientId: clientId, clientName: clientName, siteId: siteId, siteName: siteName, siteAddress: siteAddress, state: state, country: country, lat: lat, lon: lon }

    if (tags) {
      if (!manualSettings) {
        this.settings['tags_values'].value = tags.tags
        this.broadcastSettingsToClients();
        tags = tags.tags
      }
      let pointTags = {}
      if (isJSON(tags)) {
        if (Object.keys(tags).length > 0) { //make sure array isnt empty 
          tags.forEach(x => { pointTags[x] = x })
          const out = { ...pointArr, ...pointTags }
          this.setOutputData(0, out);
        }
      }

    }
  }
  onAfterSettingsChange() {
    this.onInputUpdated();
  }


}
Container.registerNodeType('history/point-tags', PointTagNode);
